.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  width: 200px;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 32px 19px 40px;
  display: flex;
}

.card__content {
  padding-inline: 16px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
}

.card__review {
  justify-content: space-between;
  margin-bottom: 22px;
  display: flex;
}

.card__review-text {
  padding-block: 2px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 14px;
  display: flex;
}

.card__price-text {
  color: #616070;
  padding-block: 1px;
  font-size: 12px;
  line-height: 18px;
}

.card__price-count {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  width: 100%;
  margin-bottom: 16px;
}

.card__button-link {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  padding-block: 12px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.card__button-link:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.stars__star:nth-child(-n+1), .stars__star:nth-child(-n+2), .stars__star:nth-child(-n+3), .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  margin: 0;
  padding: 0;
}

body {
  color: #060b35;
  font-family: Roboto, Arial, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}
